*,
*:before,
*:after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    text-align: center;
    font-weight: 400;
    color: #fff;
    background: blueviolet;
    font-size: 27px;
    padding: 5px 0;
}

.inputHolder {
    margin: 15px 0;
}

.mainWrapper {
    padding: 0 10px;
}

.mainContent {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}
   

.inputHolder { 
    padding: 20px;
    display: flex; 
    margin-left: 15px;
}
    input[type='text'] {
        max-width: 400px;
        width: 25%;
        padding: 5px 10px;
        font-size: 16px;
        border-radius: 0;
        display: inline-block;
        border: 1px solid #222;
        outline: none;
        box-shadow: 3px 1px 0px 2px, 0px -1px 0px 2px rgba(0, 0, 0, 0.62);
        font-family: 'Knewave', cursive;
        color:#000;
        text-transform: uppercase;
        /*transform: skew(-3deg, 1deg);*/
    }

    .uploadInput { 
        width: 0px;
        height: 0px;
        visibility: hidden;
        opacity: 0;
    }

    .or {
        align-items: center;
        display: inline-flex;
        padding: 0 20px;
    }

    .uploadImage {
        padding: 8px 16px;
        font-size: 16px;
        background: rgb(28, 3, 51);
        color: #fff;
        display: inline-block;
        border: 0;
        border-radius: 5px;
        cursor: pointer;
        outline: none;
    }
    .uploadImage:hover{
        background: rgb(167, 94, 236);
        color:#000;
    }


.imageHolder {
    width: 60%;
    display: block;
    flex: 0 0 60%;
    max-width: 900px;
    height: 70vmin;
}

   
.imageHolder:empty {
        display: none;
    }

    .imageHolder img {
        display: block;
        width: 60%;
        height: 100%;
        object-fit:cover;
        margin-left: 30px;
    }
     
.resultsHolder {
    padding: 30px;
}
    @media (max-width: 800px) {
        .resultsHolder {
        width: 100%;
        padding: 0;
     }
     .imageHolder img{
        display: block;
        width: 100%;
        height: 100%;
        object-fit:cover;
        margin:0px;
     }
     .inputHolder { 
        padding: 0 10px;
        display: flex; 
        margin-left: 0;
    }
    input[type='text'] {
        max-width: 400px;
        width: 100%;
    }
    .imageHolder {
        width: 100%;
        display: block;
        flex: 0 0 60%;
        max-width: 900px;
        height: 50%;
    }
    
}
    .result {
        border: 1px solid #333;
        margin: 0 0 10px;
        padding: 10px;
    }
        .result:first-of-type {
            border-color: rgb(37, 12, 59);
            background-color: rgb(173, 103, 240);
        }
            .name { color: rgb(20, 19, 19); }
            .confidence { color: #080808; }
        

        .name {
            font-size: 20px;
            font-weight: 600;
            text-transform: uppercase;
        }

        .confidence {
            display: block;
            font-weight: 300;

        }    .bestGuess {
                background: #fff;
                padding: 2px 6px;
                color: #333;
                margin-left: 3px;
            }
        
    

.button {
    padding: 12px 17px;
    font-size: 18px;
    background: blueviolet;
    color: #fff;
    display: inline-block;
    border: 0;
    border-radius: 5px;
    cursor: pointer;
    outline: none;
    margin-left: 30px;
    margin-top: 30px;
}
.button:hover{
    background-color: rgb(28, 3, 51);
}

.recentPredictions {
    margin: 40px 0 0;
    background: #dac2ff;
    padding: 10px;
}
    h2 {
        color: #111;
        font-size: 26px;
        margin: 10px  20px;
    }
    
    .recentImages {
        display: flex;
        flex-wrap: wrap;
    }

    .recentPrediction {
        flex: 0 0 20%;
        cursor: pointer;
        padding: 20px;
    }
        @media (max-width: 1100px) {
            .recentPrediction {
            flex: 0 0 25%;}
        }

        @media (max-width: 870px) {
            .recentPrediction {
            flex: 0 0 33.3%;}
        }

        @media (max-width: 700px) {
            .recentPrediction {
            flex: 0 0 50%;}

        }
    
        .recentPrediction img {
            display: block;
            width: 100%;
            height: 200px;
            object-fit: cover;
        }
            @media (max-width: 700px) {
                .img{
                height: 260px;}
            }
        
        @media (max-width:800px) { 
            .img{
            width: 100%;
            height: auto;
            margin-bottom: 15px;}
        }
      
.pred{
    color:rgb(110, 18, 196);
    padding-bottom: 4px;
}
@media (max-width: 800px) {
    .mainContent {
    flex-direction: column;
}
.button{
    margin-left: 0;
}
.recentPrediction img {
    display: block;
    width: 100%;
    height: 200px;
    object-fit: cover;
}
}